import {mapGetters} from "vuex";
import appShareQrCode from '@/components/page-component/app-share-qr-code-poster/app-share-qr-code-poster.vue';

export default {
    name: "app-details",
    components: {appShareQrCode},
    props: {
        isShowShare: Boolean,
        couponId: {
            type: String,
            default() {
                return '0';
            }
        },
        // 入口类型 0--领券中心 1--会员专享 2--我的优惠券
        type: {
            type: Number,
            default() {
                return 0;
            }
        },
        detailList: {
            type: Object,
            default() {
                return null;
            }
        }
    },
    data() {
        return {
            list: {},
            shareShow: false,
        };
    },
    computed: {
        ...mapGetters({
            userInfo: 'user/info',
        })
    },
    watch: {

        couponId() {
            if (this.couponId > 0) {
                this.getList(this.couponId);
            }
        },

        detailList() {
            if (this.detailList) {
                this.list = this.detailList;
            }
        }
    },
    created() {





    },

    onShareAppMessage() {
        return this.hShareAppMessage();
    },

    methods: {
        hShareAppMessage(s= false){
            return this.$shareAppMessage({
                title: this.list.name,
                imageUrl:"",
                path: "/pages/coupon/details/details",
                params: {
                    id: this.list.id
                }
            }, s);
        },
        getList(id) {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            let url = that.$api.coupon.detail;
            let para = {
                coupon_id: id
            };
            if (this.type === 2) {
                url = that.$api.coupon.user_coupon_detail;
                para = {
                    user_coupon_id: id
                };
            }
            that.$request({
                url: url,
                data: para
            }).then(response => {
                that.$hideLoading();
                if (response.code === 0) {
                    that.list = response.data.list;
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        },
        toGoods() {
            uni.navigateTo({
                url: this.list.page_url
            });
        },
        receive(id) {

            let that = this;
            if (this.type === 2) {
                return false;
            }
            if (that.loading) {
                return false
            }
            that.loading = true;
            uni.showLoading({
                title: '领取中...'
            });
            that.$request({
                url: that.type === 1 ? that.$api.member.receive : that.$api.coupon.receive,
                data: {
                    coupon_id: id
                },
            }).then(response => {
                that.loading = false;
                uni.hideLoading();
                if (response.code === 0) {
                    uni.showToast({
                        title: response.msg,
                        duration: 1000,
                        mask: true,
                    });
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000,
                        mask: true,
                    });
                }
                this.getList(id);
            }).catch(() => {
                uni.hideLoading();
                that.$event.on(that.$const.EVENT_USER_LOGIN).then(() => {
                    that.receive(id);
                });
            });
        },
    },
    filters: {
        reservedNum(data) {
            return Number(data);
        }
    }
}
